import React from 'react';
import { Card } from 'react-bootstrap';
import IssueFilter from './IssueFilter';
import URLSearchParams from 'url-search-params';
import { graphqlendpoint } from './graphqlendppoint';
export default class IssueReport extends React.Component{
    constructor(props){
        super(props);
        this.state={stats:''}
        this.loaddata=this.loaddata.bind(this);
    }
    componentDidMount(){
        this.loaddata();
    }
   
    componentDidUpdate(){
        if(prevProps.location.search!==this.props.location.search){
            this.loaddata();
        }
    }
    
    async loaddata(){
        const vars={};
       const {location:{search:prevsearch}}=this.props;
       const params =new URLSearchParams(prevsearch);
        vars.status= params.get('status');
        vars.effortmin=params.get('effortmin');
        vars.effortmax=params.get('effortmax');
        const query=`query IssueCount( $status: statustype, $effortmin: Int, $effortmax: Int) {
            issueCount( status: $status, effortmin: $effortmin, effortmax: $effortmax) {
              Assigned
              Closed
              Fixed
              New
              owner
            }
          }`;

          const data=await graphqlendpoint(query,vars);
          if(data){
            this.setState({stats:data.issueCount});
          }
          else{
            this.setState({stats:''});
          }
    
    }
    
    render(){
        return(
            <>
             <Card >
                    <Card.Title>Filter</Card.Title>
                    <Card.Body >
                <IssueFilter urlbase="/report"/>
                    </Card.Body>
                </Card>
                
            </>
        )
    }
}

